html {
  font-size: 62.5%;
}

body {
  font-family: 'Netflix Sans', sans-serif;
  background-color: #fff;
  color: #0f0f0f;
  overflow: hidden;
}

#container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  height: 85vh;
}

#container #content {
  max-width: 500px;
  margin: 0 50px;
}

#container #content #banner {
  display: flex;
  align-items: center;
}

#container #content #banner #project {
  font-size: 1.6rem;
  font-weight: bold;
  line-height: 1.8rem;
  margin: 1rem;
}

#container #content h1 {
  font-size: 4rem;
  font-weight: bold;
  line-height: 5rem;
  margin: 3rem 0;
}

#container #content p {
  font-size: 1.6rem;
  line-height: 2.4rem;
  margin: 3rem 0;
}

button {
  padding: 1rem 1.2rem;
  margin-right: 1rem;
  height: 4rem;
  border-radius: 4px;
  font-size: 1.3rem;
}

button:hover {
  cursor: pointer;
}

button.primary {
  background-color: #4061e7;
  border: 1px solid #4061e7;
  color: #fff;
}

button.secondary {
  background-color: transparent;
  border: 1px solid #ffffff66;
  color: #000;
}

a.button {
  padding: 1rem 1.2rem;
  margin-right: 1rem;
  text-decoration: none;
  height: 4rem;
  border-radius: 4px;
  font-size: 1.3rem;
}

a.button.primary {
  background-color: #4061e7;
  border: 1px solid #4061e7;
  color: #fff;
}

a.button.secondary {
  background-color: transparent;
  border: 1px solid #ffffff66;
  color: #000;
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: #161616;
    color: #ffffffe5;
  }
  button.secondary {
    color: #fff;
  }
  a.button.secondary {
    color: #fff;
  }
}

@media (max-width: 1120px) {
  #container {
    flex-direction: column-reverse;
    justify-content: center;
  }
  #container #content {
    margin: 0 1rem;
    text-align: center;
  }
  #container #content #banner {
    visibility: hidden;
    display: none;
  }
  #container #content h1 {
    font-size: 3.3rem;
    margin: 2rem 0;
    line-height: 3.6rem;
  }
  #container #content p {
    font-size: 1.6rem;
    margin: 2rem 0;
    line-height: 2rem;
  }
}

@media (max-width: 600px) {
  #container #content h1 {
    font-size: 2.4rem;
    margin: 1rem 0;
    line-height: 2.6rem;
  }
  #container #content p {
    font-size: 1.2rem;
    margin: 1rem 0;
    line-height: 1.6rem;
  }
}

@media (max-width: 250px) {
  #icon img {
    display: none;
    visibility: hidden;
  }
}

#reference:not(.visible) {
  visibility: hidden;
  display: none;
}

button#copy:not(.visible) {
  visibility: hidden;
}
